* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root{
  background-color: hsl(222, 26%, 31%);
  height: 100vh;
  font-weight: 700;
  position: relative;
}

#container-div{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 30%;
}


#delete{
  background-color: hsl(222, 26%, 31%);
  border-radius: 10px;
  color:white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;

}
#resetBtn{
  grid-row: 5/6;
  grid-column: 1/3;
  background-color: hsl(222, 26%, 31%);
  border-radius: 10px;
  color:white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
#equalsBtn{
  grid-row: 5/6;
  grid-column: 3/5;
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: hsl(6, 63%, 50%);
  color:white;
  cursor: pointer;
}
#theme-names{
  margin-left:10px;
}
#top-component{
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;
  align-items: center;
}
#logoName{
  font-size: 25px;
  color:white;
}

.numbers {
  width: 70px;
  text-align: center;
  border-radius: 10px;
  font-size: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

#creator{
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 20px;
  color:white;
  
}


@media (max-width:541px) {
  #container-div{
    width:70%;
  }
}
@media(max-width:500px){
  #container-div{
    width:100%;
  }
}
@media(min-width:767px) and (max-width:1025px){
  #container-div{
    width:50%;
  }
}
@media(min-width:1430px){
  #container-div{
    width:30%;
  }
}
@media(max-width:375px){
  /* change height of buttons */
  .numbers{
    width:65px;
  }
}
@media(max-width:321px){
  .numbers{
    width:55px;
  }
}
@media(max-width:300px){
  .numbers{
    width:45px;
  }
}